<template>
  <div class="modal modal-xl" tabindex="-1" id="productModal">
    <div class="modal-dialog">
      <div class="modal-content my-auto">
        <div class="modal-header bg-dark text-white">
          <h5 class="modal-title">新增產品</h5>
          <button type="button" class="btn-close btn-close-white" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body">
          <form id="productForm" class="contianer needs-validation" novalidate>
            <div class="row">
              <!-- 圖片資料 -->
              <div class="col-lg-4 p-3">
                <label class="form-label w-100 mb-3"><strong>輸入圖片連結</strong>
                  <input type="text" class="form-control" placeholder="請輸入圖片連結"
                  v-model="productModified.imageUrl">
                </label>
                <label class="form-label w-100 mb-3"><strong>或 上傳圖片</strong>
                  <input type="file" class="form-control" ref="imgInput">
                </label>
                <button type="button" class="btn btn-outline-primary rounded-0 w-100" @click="imgUpload" >新增圖片</button>
              </div>
              <!-- 產品基本資料 -->
              <div class="col-lg-8">
                <div class="row g-2 border-bottom p-2 mb-2">
                  <label class="form-label w-100">
                    <strong>標題<span class="text-muted ms-1">*必填</span></strong>
                    <input type="text" class="form-control" placeholder="請輸入標題" required v-model="productModified.title">
                    <div class="invalid-feedback">
                      此欄位為必填
                    </div>
                  </label>
                  <label class="form-label col-md-6">
                    <strong>分類<span class="text-muted ms-1">*必填</span></strong>
                    <input type="text" class="form-control" placeholder="請輸入分類" required v-model="productModified.category">
                    <div class="invalid-feedback">
                      此欄位為必填
                    </div>
                  </label>
                  <label class="form-label col-md-6">
                    <strong>單位<span class="text-muted ms-1">*必填</span></strong>
                    <input type="text" class="form-control" placeholder="請輸入單位" required v-model="productModified.unit">
                    <div class="invalid-feedback">
                      此欄位為必填
                    </div>
                  </label>
                  <label class="form-label col-md-6">
                    <strong>原價<span class="text-muted ms-1">*必填</span></strong>
                    <input type="number" class="form-control" placeholder="請輸入原價" required v-model="productModified.origin_price">
                    <div class="invalid-feedback">
                      此欄位為必填
                    </div>
                  </label>
                  <label class="form-label col-md-6">
                    <strong>售價<span class="text-muted ms-1">*必填</span></strong>
                    <input type="number" class="form-control" placeholder="請輸入售價" required v-model="productModified.price">
                    <div class="invalid-feedback">
                      此欄位為必填
                    </div>
                  </label>
                </div>
                <!-- 產品描述 -->
                <div class="row g-1">
                  <label class="form-label w-100"><strong>產品描述</strong>
                    <textarea class="form-control" placeholder="請輸入產品描述" cols="30" rows="2" v-model="productModified.description"></textarea>
                  </label>
                  <label class="form-label w-100"><strong>說明內容</strong>
                    <textarea class="form-control" placeholder="請輸入說明內容" cols="30" rows="2" v-model="productModified.content"></textarea>
                  </label>
                </div>
              </div>
            </div>
          </form>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-outline-secondary" data-bs-dismiss="modal">取消</button>
          <button type="button" @click="$emit('sentData',productModified)" class="btn btn-primary" form="productForm">確認</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Modal from 'bootstrap/js/dist/modal'
export default {
  data () {
    return {
      productModal: {},
      productModified: {}
    }
  },
  props: ['productsReceived'],
  watch: {
    productsReceived () {
      this.productModified = this.productsReceived
    }
  },
  methods: {
    modalShow () {
      console.log(this.productModal)
      this.productModal.show()
    },
    modalHide () {
      this.productModal.hide()
    },
    confirm () {
      console.log('validation')
      // 表單驗證
      const forms = document.querySelectorAll('.needs-validation')
      Array.prototype.slice.call(forms)
        .forEach(function (form) {
          form.addEventListener('submit', function (event) {
            if (!form.checkValidity()) {
              event.preventDefault()
              event.stopPropagation()
            }
            form.classList.add('was-validated')
          }, false)
        })
    },
    imgUpload () {
      const ImgInputFile = this.$refs.imgInput.files[0]
      console.dir(ImgInputFile)
      const data = new FormData()
      data.append('file-to-upload', ImgInputFile)
      // 上傳圖片
      const api = `${process.env.VUE_APP_PATH}api/${process.env.VUE_APP_API}/admin/upload`
      this.axios.post(api, data).then((res) => {
        console.log(res)
        if (res.data.sucess) {
          this.productModified.imageUrl = res.data.imageUrl
        }
      })
    }
  },
  mounted () {
    this.productModal = new Modal(document.getElementById('productModal'))
  },
  created () {

  }
}
</script>
