<template>
  <loading v-model:active="isLoading"/>
  <div class="container mt-5 mb-5">
    <button type="button" class="btn btn-primary mb-3 d-flex ms-auto" @click="openModal(true)">新增產品</button>
    <table class="table border">
      <thead>
        <tr>
          <th scope="col">分類</th>
          <th scope="col">產品名稱</th>
          <th scope="col">原價</th>
          <th scope="col">售價</th>
          <th scope="col">是否啟用</th>
          <th scope="col">編輯</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item) in products" :key="item.id">
          <td>{{ item.category }}</td>
          <td>{{ item.title }}</td>
          <td>{{ item.origin_price }}</td>
          <td>{{ item.price }}</td>
          <td>
            <span v-if="item.is_enabled" class="text-primary">已啟用</span>
            <span v-else class="text-muted">未啟用</span>
          </td>
          <td>
            <div class="btn-group">
              <button type="button" class="btn btn-outline-primary" @click="openModal(item)">編輯</button>
              <button type="button" class="btn btn-outline-danger" @click="deleteProduct(item.id)">刪除</button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
    <pageButton :paginationReceived="pagination" @changePage="getProducts"></pageButton>
  </div>
  <productModal ref="productModal" :productsReceived="tempProduct" @sentData="updateData"></productModal>
  <Toast ref="toast" class="position-absolute top-0 end-0"></Toast>
</template>

<script>
import productModal from '../components/ProductsModal.vue'
// 讀取中樣式
import Loading from 'vue-loading-overlay'
import 'vue-loading-overlay/dist/css/index.css'
// 分頁按鈕
import pageButton from '../components/PageButton.vue'
// 吐司元件
import Toast from '../components/Toast.vue'

export default {
  data () {
    return {
      products: [],
      tempProduct: {},
      isNew: true,
      isLoading: false,
      pagination: {}
    }
  },
  components: {
    productModal, Loading, pageButton, Toast
  },
  methods: {
    getProducts (page = 1) {
      this.isLoading = true
      const api = `${process.env.VUE_APP_PATH}api/${process.env.VUE_APP_API}/admin/products?page=${page}`
      // const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}admin/products/all`
      this.axios.get(api).then((res) => {
        this.isLoading = false
        this.products = res.data.products
        this.pagination = res.data.pagination
      })
    },
    openModal (openData) {
      if (openData === true) {
        this.isNew = true
        this.tempProduct = {}
      } else {
        this.isNew = false
        this.tempProduct = openData
      }

      this.$refs.productModal.modalShow()
    },
    openToast (success, message) {
      console.log(success, message)
      this.$refs.toast.toastShow(success, message)
    },
    updateData (item) {
      if (this.isNew === true) {
        this.$refs.productModal.modalHide()
        const data = { data: item }
        const api = `${process.env.VUE_APP_PATH}api/${process.env.VUE_APP_API}/admin/product`
        this.axios.post(api, data).then((res) => {
          console.log(res)
          this.openToast(res.data.success, res.data.message)
          this.getProducts()
        })
      } else if (this.isNew === false) {
        this.$refs.productModal.modalHide()
        const data = { data: item }
        const api = `${process.env.VUE_APP_PATH}api/${process.env.VUE_APP_API}/admin/product/${item.id}`
        console.log(data)
        console.log(api)
        this.axios.put(api, data).then((res) => {
          console.log(res)
          this.openToast()
          this.getProducts()
        })
      }
    },
    deleteProduct (id) {
      const api = `${process.env.VUE_APP_PATH}api/${process.env.VUE_APP_API}/admin/product/${id}`
      console.log(api)
      this.axios.delete(api).then((res) => {
        console.log(res)
        this.getProducts()
      })
    }
  },
  created () {
    this.getProducts()
  }
}
</script>
